html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  background-color: snow;
  color: black;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
img {
  align-self: stretch;
  width: 100%;
  height: 200px;
  margin: 0px;
}

p {
  margin: auto;
  text-align: left;
  font-size: 12px;
  border: 2px groove #eee;
  border-left: 6px groove #eee;
  padding-left: 8px;
  background-color: snow;
}

header {
  display: flex;
  flex-wrap: wrap;
  background-color: #19a974;
  opacity: 0.5;
  padding: 0;
  margin-bottom: 2px;
}

footer {
  clear: both;
  min-height: 50px;
  margin-top: auto;
  padding: 30px;
  color: snow;
  background-color: #9eebcf;
  text-align: center;
  font-family: cursive;
  font-style: italic;
  font-size: 15px;
}

h1 {
  display: inline-flex;
  align-self: flex-end;
  color: snow;
  padding-left: 20px;
  font-size: 50px;
  font-family: cursive;
  text-shadow: -2px 2px 0 #137752,
                2px 2px 0 #137752,
                2px -2px 0 #137752,
              -2px -2px 0 #137752;
}

input {
  display: inline-flex;
  height: 20px;
  align-self: center;
  border: none;
  border-bottom: 4px groove snow;
  border-left: 4px groove snow;
  border-right: 4px groove snow;
  background-color: #3cbc8d;
  color: snow;
  width: 25%;
  padding: 20px;
  margin-left: 20%;
}
.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 10px;
  width: 100%;
  margin-top: 20px;
}
.content-wrap {
  padding-bottom: 2.5em;
}
